<template>
    <div>
        <fullscreen ref="fullscreen">
            <div id="panel-prev" class="panel">
            <div class="panel-hdr">
                <h2><i class="fad fa-fw fa-search-plus mr-2 text-info"></i>LPA </h2>
                <div class="panel-toolbar">
                    <button class="btn btn-panel bg-transparent fs-xl w-auto h-auto rounded-0 waves-effect waves-themed" @click="handleClickMin" v-if="isShow"><i class="fal fa-fw fa-minus"></i></button>
                    <button class="btn btn-panel bg-transparent fs-xl w-auto h-auto rounded-0 waves-effect waves-themed" @click="handleClickMin" v-if="!isShow"><i class="fal fa-fw fa-plus"></i></button>
                    <button class="btn btn-panel bg-transparent fs-xl w-auto h-auto rounded-0 waves-effect waves-themed" @click="fullscreenChange"><i class="fal fa-fw fa-expand"></i></button>
                </div>
            </div>
            <div class="panel-container show" v-show="isShow">
                <div class="panel-content">
                    <div class="row">
                        <div class="col-sm-6 col-xl-6 col-lg-6 ">
                            <div class="form-group">
                                <label class="form-label"> </label>
                                <div>
                                    <el-select v-model="taskNameLike" placeholder="" style="width: 100%;">
                                        <el-option
                                                v-for="item in options"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-6 col-xl-6 col-lg-6">
                            <div class="form-group">
                                <label class="form-label"></label>
                                <div>
                                    <el-input v-model="taskAssignee" placeholder="" :readonly="!isAdmin"></el-input>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-6 col-xl-6 col-lg-6 mt-3">
                            <div class="form-group">
                                <label class="form-label"></label>
                                <div class="block">
                                    <el-date-picker
                                            v-model="dateRange"
                                            type="daterange"
                                            align="left"
                                            unlink-panels
                                            range-separator=""
                                            start-placeholder=""
                                            end-placeholder=""
                                            :picker-options="pickerOptions"
                                            style="width: 100%;">
                                    </el-date-picker>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-6 col-xl-6 col-lg-6 mt-3">
                            <div class="form-group">
                                <label class="form-label"></label>
                                <div style="text-align:right">
                                    <el-button type="primary" icon="el-icon-search" @click="emitSearch"></el-button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </fullscreen>
    </div>
</template>

<script>
    export default {
        name: "Search",
        data() {
            return{
                isShow: true,
                pickerOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                dateRange: null,
                options: [{
                    value: '',
                    label: ''
                }
                // , {
                //     value: 'A2',
                //     label: ''
                // }, {
                //     value: 'B1',
                //     label: ''
                // }
                ],
                taskNameLike: '',
                taskAssignee: '',
                isAdmin: false
            }
        },
        methods:{
            getSessionUser(){
                this.isAdmin = false
                const user = JSON.parse(sessionStorage.getItem("user"));
                const roles = user.Roles.toLocaleLowerCase()
                if (roles.indexOf('admin') != -1) {
                    this.taskAssignee = ''
                    this.isAdmin = true
                }else {
                    this.taskAssignee = user.sub
                }
            },
            emitSearch(){
                this.$emit('search',{ dateRange: this.dateRange, taskNameLike: this.taskNameLike, taskAssignee: this.taskAssignee })
            },
            handleClickMin(){
                this.isShow = !this.isShow
            },
            fullscreenChange () {
                this.$refs['fullscreen'].toggle()
            }
        },
        mounted() {
            this.getSessionUser()
        }
    }
</script>

