<template>

    <div class="d-flex flex-grow-1 p-0">
        <!-- left slider -->

        <!-- end left slider -->
        <!-- inbox container -->
        <div class="d-flex flex-column flex-grow-1 bg-white">
            <!-- inbox header -->
            <div class="flex-grow-0">
                <!-- inbox button shortcut -->

                <!-- end inbox button shortcut -->
            </div>
            <!-- end inbox header -->
            <!-- inbox message -->
            <div class="flex-wrap align-items-center flex-grow-1 position-relative bg-gray-100">
                <div class="position-absolute pos-top pos-bottom w-100">
                    <div class="d-flex h-100 flex-column">
                        <div style="padding: 10px 10px 10px 10px">
                            <el-row :gutter="20">
                                <el-col :span="windowWidth>1000? 14:24">
                                    <Search @search="search"></Search>
                                    <Result @eventInfo="eventInfo" :task-list="taskList" :query-params="queryParams"></Result>
                                </el-col>
                                <el-col :span="windowWidth>1000? 10:24">
                                    <Detail :task-detail="taskDetail" :windowWidth="windowWidth"></Detail>
                                </el-col>
                            </el-row>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Search from './components/Search'
    import Result from './components/Result'
    import Detail from './components/Detail'
    import { getHistoryTaskList, getAHistoryTask } from '@/api/LPATask'
    import moment from 'moment'

    export default {
        name: "Home",
        components:{ Search, Result, Detail },
        data () {
            return{
                windowWidth: document.documentElement.clientWidth,
                taskList: [],
                taskDetail: {},
                queryParams:{
                    start: 0,
                    size: 10000,
                    active: true,
                    taskNameLike: '%lpa%'
                }
            }
        },
        methods:{
            getHistoryTaskList() {
                const user = JSON.parse(sessionStorage.getItem("user"));
                const roles = user.Roles.toLocaleLowerCase()
                if (roles.indexOf('admin') != -1) {

                }else {
                    this.queryParams.taskAssignee = user.sub
                }
                getHistoryTaskList(this.queryParams).then(response => {
                    this.taskList = response.data
                }).catch(() => {

                })
            },
            search(data) {
                this.queryParams = {
                    start: 0,
                    size: 10000,
                    active: true,
                    taskNameLike: '%lpa%'
                }
                var start = ''
                var end = ''
                if (data.dateRange != null ){
                    start = moment(data.dateRange[0]).utc().format("YYYY-MM-DDTHH:mm:ss[Z]")
                    end = moment(data.dateRange[1]).utc().format("YYYY-MM-DDTHH:mm:ss[Z]")
                    this.queryParams.dueDateAfter = start
                    this.queryParams.dueDateBefore = end
                }
                if (data.taskNameLike != ""){
                    this.queryParams.taskNameLike = data.taskNameLike
                }
                if (data.taskAssignee != ""){
                    this.queryParams.taskAssignee = data.taskAssignee
                }
                getHistoryTaskList(this.queryParams).then(response => {
                    this.taskList = response.data
                }).catch(() => {

                })
            },
            eventInfo(data){
                getAHistoryTask(data).then(response =>{
                    this.taskDetail = response
                }).catch(() => {

                })
            }
        },
        mounted() {
            this.getHistoryTaskList()
            // 
            window.onresize = () => {
                return (() => {
                    this.windowWidth = document.body.clientWidth
                })()
            }
        }
    }
</script>
