<template>
    <div>
        <div>
            <el-input
                    v-model="serch"
                    placeholder=""
                    prefix-icon="el-icon-search"
                    style="width: 200px;margin-right: 10px">
            </el-input>
        </div>
        <div>
            <el-table
                    :data="filterData"
                    @row-click="handleClick"
                    style="width: 100%">
                <el-table-column
                        sortable
                        prop="processInstanceId"
                        label="ID"
                        width="100">
                </el-table-column>
                <el-table-column
                        sortable
                        prop="name"
                        label=""
                        :show-overflow-tooltip="true">
                </el-table-column>
                <el-table-column
                        sortable
                        prop="assignee"
                        label=""
                        width="100">
                </el-table-column>
                <el-table-column
                        sortable
                        prop="dueDate"
                        label=""
                        width="180">
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i>
                        <span style="margin-left: 5px">{{ formatDate(scope.row.dueDate) }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        sortable
                        prop="endTime"
                        label=""
                        width="180">
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i>
                        <span style="margin-left: 5px">{{ formatDate(scope.row.endTime) }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        sortable
                        prop="status"
                        label=""
                        width="100">
                    <template slot-scope="scope">
                        <el-tag
                                :type="judgeType(judgeStatus(scope.row))"
                                disable-transitions>{{judgeStatus(scope.row)}}</el-tag>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div>
            <pagination
                    v-show="this.taskList.length>0"
                    :total="this.taskList.length"
                    :page.sync="listQuery.start"
                    :limit.sync="listQuery.size"
                    @pagination="pagination"
                    style="padding: 20px 0px 0px 0px;"
            />
        </div>
    </div>
</template>

<script>
    import moment from 'moment'
    import Pagination from '@/components/Pagination'
    import { getHistoryTaskList } from '@/api/LPATask'

    export default {
        name: "TableList",
        props: [ 'taskList', 'queryParams' ],
        components: {
            Pagination
        },
        data() {
            return {
                listQuery: {
                    active: true,
                    start: 0,
                    size: 10000
                },
                tableData: [],
                serch: ''
            }
        },
        computed: {
            //
            filterData: function() {
                var input = this.serch && this.serch.toLowerCase();
                var items = this.tableData;
                var items1;
                if (input) {
                    items1 = items.filter(function(item) {
                        return Object.keys(item).some(function(key1) {
                            return String(item[key1])
                                .toLowerCase()
                                .match(input);
                        });
                    });
                } else {
                    items1 = items;
                }
                return items1;
            }
        },
        methods: {
            formatDate(date){
                if (date === undefined || date === null){
                    return ''
                }
                return moment(date).format('YYYY-MM-DD HH:mm:ss')
            },
            judgeStatus(row){
                const end = row.endTime
                const due = row.dueDate
                if (due === undefined || due === null){
                    if (end === undefined || end === null){
                        return ''
                    }else{
                        return ''
                    }
                }else{
                    if (end === undefined || end === null){
                        return ''
                    }else if (end<=due){
                        return ''
                    }else if (end>due){
                        return ''
                    }
                }
            },
            judgeType(status){
                if (status === ''){
                    return 'success'
                }else if (status === ''){
                    return 'primary'
                }else if (status === ''){
                    return 'danger'
                }
            },
            pagination(data){
                this.listQuery.start = data.page;
                this.listQuery.size = data.limit;
                getHistoryTaskList(this.listQuery).then(response => {
                    this.tableData = response.data
                }).catch(() => {

                })
            },
            handleClick(row){
                this.$emit('eventInfo', row.id)
            },
            getHistoryTaskList() {
                this.listQuery = {
                    active: true,
                    start: 0,
                    size: 10000
                }
                this.listQuery.taskNameLike = this.queryParams.taskNameLike
                if (this.queryParams.dueDateAfter){
                    this.listQuery.dueDateAfter = this.queryParams.dueDateAfter
                    this.listQuery.dueDateBefore = this.queryParams.dueDateBefore
                }
                const user = JSON.parse(sessionStorage.getItem("user"));
                const roles = user.Roles.toLocaleLowerCase()
                if (roles.indexOf('admin') != -1) {

                }else {
                    this.listQuery.taskAssignee = user.sub
                }
                getHistoryTaskList(this.listQuery).then(response => {
                    this.tableData = response.data
                }).catch((err) => {
                    console.log(err)
                })
            }
        },
        watch:{
            queryParams(){
                this.getHistoryTaskList()
            }
        },
        mounted() {
            this.getHistoryTaskList()
        }
    }
</script>

<style lang="stylus" scoped>
    .el-table .warning-row {
        background: oldlace;
    }

    .el-table .success-row {
        background: #f0f9eb;
    }
</style>