<template>
    <fullscreen ref="fullscreen">
        <div id="panel-calendar" class="panel">
        <div class="panel-hdr">
            <h2> </h2>
            <div class="panel-toolbar">
                <button class="btn btn-panel bg-transparent fs-xl w-auto h-auto rounded-0 waves-effect waves-themed" @click="handleClickMin" v-if="isShow"><i class="fal fa-fw fa-minus"></i></button>
                <button class="btn btn-panel bg-transparent fs-xl w-auto h-auto rounded-0 waves-effect waves-themed" @click="handleClickMin" v-if="!isShow"><i class="fal fa-fw fa-plus"></i></button>
                <button class="btn btn-panel bg-transparent fs-xl w-auto h-auto rounded-0 waves-effect waves-themed" @click="fullscreenChange"><i class="fal fa-fw fa-expand"></i></button>
            </div>
        </div>
        <div class="panel-container show" v-show="isShow">
            <div class="panel-content">
                <el-tabs v-model="activeName">
                    <el-tab-pane  name="calendar">
                        <span slot="label"><i class="fad fa-fw fa-calendar-day mr-2 text-info"></i></span>
                        <div>
                            <Calendar v-if="activeName === 'calendar'" @eventInfo="eventInfo" :task-list="taskList"></Calendar>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane  name="list">
                        <span slot="label"><i class="fad fa-fw fa-table mr-2 text-info"></i></span>
                        <div>
                            <TableList v-if="activeName === 'list'" @eventInfo="eventInfo" :task-list="taskList" :query-params="queryParams"></TableList>
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </div>
    </div>
    </fullscreen>
</template>

<script>
    import Calendar from './Calendar'
    import TableList from "./TableList";

    export default {
        name: "Result",
        components: { TableList, Calendar },
        props:{
            taskList: Array,
            queryParams: Object
        },
        data() {
            return{
                isShow: true,
                activeName: "calendar"
            }
        },
        methods:{
            eventInfo(data){
                this.$emit('eventInfo', data)
                if (this.$refs['fullscreen'].isFullscreen){
                    this.$refs['fullscreen'].toggle()
                }
            },
            handleClickMin(){
                this.isShow = !this.isShow
            },
            fullscreenChange () {
                this.$refs['fullscreen'].toggle()
            }
        }
    }
</script>

