<template>
    <FullCalendar ref="calendar" :options="calendarOptions"/>
</template>

<script>
    import FullCalendar from '@fullcalendar/vue'
    import dayGridPlugin from '@fullcalendar/daygrid'
    import interactionPlugin from '@fullcalendar/interaction'
    import timeGridPlugin from '@fullcalendar/timegrid'
    import listPlugin from '@fullcalendar/list'

    export default {
        name: "Calendar",
        components:{ FullCalendar },
        props: [ 'taskList' ],
        data() {
            return{
                eventsList: [],
                classNameList: [
                    'border-warning bg-warning text-dark',
                    'bg-primary border-primary text-white',
                    'bg-info border-info text-white',
                    'bg-primary text-white border-primary'
                ],
                calendarOptions: {
                    plugins: [ dayGridPlugin, interactionPlugin, timeGridPlugin, listPlugin ],
                    timeZone: 'local',
                    initialView: 'dayGridMonth',
                    locale: 'zh-cn',
                    aspectRatio: 1.2,
                    headerToolbar: { // 
                        left: 'prev next',
                        center: 'title',
                        right: 'today dayGridMonth,timeGridWeek,timeGridDay'
                    },
                    buttonText:{  //
                        today: '',
                        month: '',
                        week: '',
                        day: ''
                    },
                    dayMaxEvents: true,
                    eventClick: this.handleEventClick,
                    events: this.eventsList,
                    customButtons: {
                        prev: { // this overrides the prev button
                            text: "PREV",
                            click: () => {
                                console.log("eventPrev")
                                let calendarApi = this.$refs.calendar.getApi()
                                calendarApi.prev()
                            }
                        },
                        next: { // this overrides the next button
                            text: "NEXT",
                            click: () => {
                                console.log("eventNext")
                                let calendarApi = this.$refs.calendar.getApi()
                                calendarApi.next()
                            }
                        }
                    }
                }
            }
        },
        methods: {
            handleEventClick(info) {
                this.$emit('eventInfo', info.event.id)
            },
            getTaskList() {
                this.eventsList = []
                this.taskList.forEach((item,index) => {
                    var className = this.classNameList[0]
                    if (item.endTime != null && item.dueDate != null){
                        if (item.endTime > item.dueDate){
                            className = 'bg-danger border-danger text-white'
                        }else{
                            className = 'bg-success border-success text-white'
                        }
                    }else{
                        className = this.classNameList[index%4]
                    }
                    const event = {
                        id: item.id,
                        title: item.name,
                        start: item.dueDate,
                        end: item.endTime,
                        className: className
                    }
                    this.eventsList.push(event)
                })
                this.calendarOptions.events =  this.eventsList
            }
        },
        watch: {
            taskList() {
                this.getTaskList()
            }
        },
        mounted() {
            this.getTaskList()
        }
    }
</script>

