<template>
    <fullscreen ref="fullscreen">
        <div id="panel-1" class="panel">
        <div class="panel-hdr">
            <h2> </h2>
            <div class="panel-toolbar">
                <button class="btn btn-panel bg-transparent fs-xl w-auto h-auto rounded-0 waves-effect waves-themed" @click="handleClickMin" v-if="isShow"><i class="fal fa-fw fa-minus"></i></button>
                <button class="btn btn-panel bg-transparent fs-xl w-auto h-auto rounded-0 waves-effect waves-themed" @click="handleClickMin" v-if="!isShow"><i class="fal fa-fw fa-plus"></i></button>
                <button class="btn btn-panel bg-transparent fs-xl w-auto h-auto rounded-0 waves-effect waves-themed" @click="fullscreenChange"><i class="fal fa-fw fa-expand"></i></button>
            </div>
        </div>
        <div class="panel-container show" v-show="isShow">
            <div class="panel-content p-3" style="max-height: 1000px; overflow:scroll; overflow-y:visible; overflow-x: visible" v-show="taskDetail.processInstanceId != null">
                <h6> {{ taskDetail.processInstanceId }}/{{ taskDetail.name }}</h6>
                <h6> {{ taskDetail.id }}/{{ taskDetail.taskDefinitionKey }}</h6>
                <h6>/ {{ formatDate(taskDetail.dueDate) }}/ <span class="fw-900">{{ formatDate(taskDetail.endTime) }}</span> <span class="badge ml-3"><el-tag :type="calcTime(taskDetail.dueDate,taskDetail.endTime).type" effect="dark">{{ calcTime(taskDetail.dueDate,taskDetail.endTime).label }}</el-tag></span></h6>
                <h6> {{ userInfo.firstName }} {{ userInfo.lastName }}</h6>
                <ul class="nav nav-tabs nav-tabs-clean mt-6" role="tablist">
                    <li class="nav-item"><a class="nav-link active" data-toggle="tab" href="#tab-task" role="tab"><i class="fal s fa-fw fa-file-edit mr-2"></i>LPA</a></li>
                    <li class="nav-item"><a class="nav-link" data-toggle="tab" href="#tab-issue" role="tab"><i class="fal fa-fw fa-question-square mr-2"></i></a></li>
                </ul>
                <div class="tab-content pl-0 pr-0 pt-3">
                    <div class="tab-pane fade show active " id="tab-task" role="tabpanel" aria-labelledby="tab-task" style="max-height: 700px;overflow: auto">
                        <div v-for="detail in details">
                            <el-divider content-position="left">{{ detail.question }}</el-divider>
                            <div>
                                <el-form ref="form" label-width="">
                                    <el-form-item label="" style="margin-bottom: 0px">
                                        <el-tag :type="judgeAnswer(detail.answer)">{{ detail.answer }}</el-tag>
                                    </el-form-item>
                                    <el-form-item label="" style="margin-bottom: 0px">
                                        {{ detail.comments }}
<!--                                        <el-input type="textarea" readonly v-model="detail.comments"></el-input>-->
                                    </el-form-item>
                                    <el-form-item label="" v-show="detail.attachment != '[]'" style="margin-bottom: 0px">
                                        <el-button type="primary" size="small" @click="openDownloadDialog(detail.attachment)"></el-button>
                                    </el-form-item>
                                    <el-form-item label="" v-show="detail.assignto != null" style="margin-bottom: 0px">
                                        {{ detail.assignto }}
                                    </el-form-item>
                                </el-form>
                            </div>
                        </div>
                    </div>
                    <div class="tab-pane fade " id="tab-issue" role="tabpanel" aria-labelledby="tab-issue">
                        <div class="list-group shadow-sm">
                            <a href="#" class="list-group-item list-group-item-action" data-toggle="modal" data-target=".issue-modal" v-for="problem in problematicSorted" @click="handleClickProblem(problem)">
                                <div class="d-flex w-100 justify-content-between">
                                    <h5 class="mb-1">{{ problem.question }}</h5>
                                    <el-tag :type="judgeCoreactionType(problem)">{{ judgeCoreaction(problem) }}</el-tag>
                                </div>
                                    <small>{{ problem.assignto }}  {{ problem.assigneeUserName }}/ : {{ formatDateTimestamp(problem.lpaDealTime) }}</small>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div>
        <el-dialog
                title=""
                :visible.sync="downloadDialogVisible"
                :width="windowWidth>1000? '40%':'100%'"
                center>
            <span v-for="item in attachment">
                <el-button @click="download(item)" type="primary" style="margin-right: 10px;margin-bottom: 10px">{{ item.name }}<i class="el-icon-download el-icon--right"></i></el-button>
            </span>
            <span slot="footer" class="dialog-footer">
            </span>
        </el-dialog>
        <el-drawer
                title=""
                :size="windowWidth>1000? '35%':'90%'"
                :with-header="true"
                :visible.sync="drawer"
                :direction="direction">
            <div style="margin-left: 20px">
                <el-form ref="form" label-width="">
                    <el-form-item label="">
                        {{ this.problemDetail.question }}
                    </el-form-item>
                    <el-form-item label="">
                        {{ this.problemDetail.lpaDeal }}
                    </el-form-item>
                    <el-form-item label="" v-show="this.problemDetail.lpaDealAttachment && this.problemDetail.lpaDealAttachment != '[]'">
                        <el-button type="primary" size="small" @click="openDownloadDialog2()"></el-button>
                    </el-form-item>
                    <el-form-item label="">
                        {{ this.problemDetail.assignto }}
                    </el-form-item>
                    <el-form-item label="">
                        {{ this.problemDetail.assigneeUserName }}
                    </el-form-item>
                    <el-form-item label="">
                        {{ judgeCoreaction(this.problemDetail) }}
                    </el-form-item>
                    <el-form-item label="">
                        {{ this.problemDetail.comments}}
                    </el-form-item>
                    <el-form-item label="">
                        {{ formatDateTimestamp(this.problemDetail.lpaDealTime) }}
                    </el-form-item>
                </el-form>
            </div>
        </el-drawer>
    </div>
    </fullscreen>
</template>

<script>
    import moment from 'moment'
    import axios from 'axios'
    import config from '@/common/config'
    import { getUserInfoById } from '@/api/user'
    import { getDetail } from '@/api/LPATask'

    export default {
        name: "Detail",
        props: ['taskDetail', 'windowWidth'],
        data() {
            return{
                isShow: true,
                userInfo: '',
                detailParams:{},
                details: [],
                attachment: '',
                downloadDialogVisible: false,
                drawer: false,
                direction: 'ltr',
                problematic: [],
                problemDetail: {}
            }
        },
        computed:{
            problematicSorted() {
                return this.sortByKey(this.problematic,'subProcessInstanceId')
            }
        },
        methods:{
            formatDate(date){
                if (date === undefined || date === null){
                    return ''
                }
                return moment(date).format('YYYY-MM-DD HH:mm:ss')
            },
            formatDateTimestamp(date){
                if (date === undefined || date === null){
                    return ''
                }
                return moment(Number(date)).format('YYYY-MM-DD HH:mm:ss')
            },
            sortByKey(array,key){
                return array.sort(function(a,b){
                    var x = a[key];
                    var y = b[key];
                    return ((x<y)?-1:(x>y)?1:0)
                })
            },
            handleClickMin(){
                this.isShow = !this.isShow
            },
            fullscreenChange () {
                this.$refs['fullscreen'].toggle()
            },
            getUserInfoById(){
                getUserInfoById(this.taskDetail.assignee).then(response => {
                    this.userInfo = response
                }).catch(() => {

                })
            },
            calcTime(dueDate,endDate){
                if (dueDate != null && endDate !=null){
                    if (dueDate >= endDate){
                        return {
                            type: "success",
                            label: ""
                        }
                    }else{
                        return {
                            type: "danger",
                            label: ","
                        }
                    }
                }else if (dueDate != null){
                    if (dueDate >= new Date() || endDate == null){
                        return {
                            type: "warning",
                            label: ""
                        }
                    }else{
                        return {
                            type: "danger",
                            label: ","
                        }
                    }
                }else{
                    return {
                        type: "warning",
                        label: ""
                    }
                }
            },
            getDetail(){
                this.details = []
                this.problematic = []
                this.detailParams = {
                    processInstanceId : this.taskDetail.processInstanceId,
                    variableName:"lpaQuestionString"
                }
                getDetail(this.detailParams).then(response => {
                    const data = JSON.parse(response.data[0].variable.value)
                    this.details = data
                    data.forEach(item => {
                        if (item.subProcessInstanceId != null){
                            getDetail({ processInstanceId: item.subProcessInstanceId }).then(res => {
                                var result = {}
                                result.subProcessInstanceId = item.subProcessInstanceId
                                result.assigneeUserName = ''
                                res.data.forEach(index => {
                                    result[index.variable.name] = index.variable.value
                                })
                                getUserInfoById(result.assigntoUser).then(userInfo => {
                                    result.assigneeUserName = userInfo.firstName + " " + userInfo.lastName
                                })
                                this.problematic.push(result)
                            })
                        }
                    })
                }).catch((err) => {
                    console.log(err)
                })
            },
            judgeAnswer(answer){
                if (answer === ''){
                    return 'info'
                } else if (answer === ''){
                    return 'success'
                } else if (answer === ''){
                    return 'danger'
                } else if (answer === 'na'){
                    return 'warning'
                }
            },
            judgeCoreactionType(problem){
                if (problem.coreaction === undefined || problem.coreaction === null){
                    if (!!problem.lpaDealTime){
                        return 'primary'
                    }else{
                        return 'warning'
                    }
                }else if (problem.coreaction === ''){
                    return 'success'
                }else if (problem.coreaction === ''){
                    return 'danger'
                }
            },
            judgeCoreaction(problem){
                if (problem.coreaction === undefined || problem.coreaction === null){
                    if (!!problem.lpaDealTime){
                        return ''
                    }else{
                        return ''
                    }
                }else if (problem.coreaction === ''){
                    return ''
                }else if (problem.coreaction === ''){
                    return ''
                }
            },
            handleClickProblem(problem){
                this.problemDetail = problem
                this.drawer = true
            },
            openDownloadDialog(params){
                this.attachment = JSON.parse(params)
                this.downloadDialogVisible = true
            },
            openDownloadDialog2(){
                this.attachment = JSON.parse(this.problemDetail.lpaDealAttachment)
                this.downloadDialogVisible = true
            },
            download(params) {
                axios({
                    method: 'get',
                    url: config.fileUrl + params.path,
                    responseType: 'blob'  //,response,BLOB([])
                }).then(response => {
                    const href = URL.createObjectURL(response.data); //
                    const a = document.createElement('a');
                    a.setAttribute('href', href);
                    a.setAttribute('download', params.name);
                    a.click();
                    URL.revokeObjectURL(href);
                    this.$message.success("");
                }).catch(err => {
                    console.log(err);
                    this.$message.error(",");
                })
            }
        },
        watch:{
            taskDetail() {
                this.getUserInfoById()
                this.getDetail()
            }
        }
    }
</script>

<style lang="stylus" scoped>

</style>